.search-div{
	width: 80%;
	min-height: 250px;
	margin: auto;
	background-color: #110F18;
	border: 2px solid black;
}
.form-div{
	display: -ms-flexbox;
	display: flex;
	-ms-flex-direction: column;
	    flex-direction: column;
	-ms-flex-align: center;
	    align-items: center;
	-ms-flex-pack: center;
	    justify-content: center;
}
.form-div h2{
	color: #666666;
	margin: 10px 0 10px 0;
	font-size: 32px;
}
.form-div input{
	font-size: 24px;
	border: 1px solid black;
	border-radius: 5px;
}
.dropdown-div h2{
	color: #666666;
	font-size: 32px;
	margin: 40px 0 0 0;
	background-color: #0C0818;
	padding: 2px;
	border: 1px solid black;
	cursor: pointer;
}
.dropdown-links{
	display: none;
	position: relative;
	background-color: #130C26;
}
.dropdown-links a{
	text-decoration: none;
	color: #666666;
	font-size: 20px;
	margin: 5px 0 5px 0;
}
.dropdown-div:hover .dropdown-links{
	display: -ms-flexbox;
	display: flex;
	-ms-flex-direction: column;
	    flex-direction: column;
	-ms-flex-align: center;
	    align-items: center;
	-webkit-box-shadow: 0px 8px 16px 0px rgba(0,0,0,0.2);
	        box-shadow: 0px 8px 16px 0px rgba(0,0,0,0.2);
}
.results-div{
	width: 80%;
	min-height: 300px;
	margin: auto;
	background-color: #110F18;
	border: 2px solid black;
	margin-top: 100px;
}
.joke-div{
	display: -ms-flexbox;
	display: flex;
	-ms-flex-pack: center;
	    justify-content: center;
	-ms-flex-align: center;
	    align-items: center;
	-ms-flex-direction: column;
	    flex-direction: column;
	opacity: 0;

	-webkit-animation-name: fade-in;

	        animation-name: fade-in;
	-webkit-animation-delay: 0.5s;
	        animation-delay: 0.5s;
	-webkit-animation-duration: 1.5s;
	        animation-duration: 1.5s;
	-webkit-animation-fill-mode: forwards;
	        animation-fill-mode: forwards;

}
.joke-div h2{
	font-size: 60px;
	margin: 10px 0 10px 0;
	text-align: center;
	font-weight: 500;
}
.color-choise{
	min-width: 175px;
	font-size: 20px;
	background-color: #0C0818;
	color: #666666;
	border: 1px solid black;
}
.color-choise:focus{
	outline-width: 0;
}

.g1{
	display: grid;
	grid-template-rows: repeat(2, 120px);
	grid-template-columns: repeat(3, 1fr);
	-ms-flex-pack: center;
	    justify-content: center;
	-ms-flex-line-pack: center;
	    align-content: center;
	margin-top: 5px;
	border-radius: 5px;
}
.g2{
	display: grid;
	grid-template-rows: 100px;
	grid-template-columns: repeat(5, 1fr);
	justify-items: center;
	-ms-flex-align: center;
	    align-items: center;
	border: 2px solid black;
}
.g2 div{
	padding: 5px;
}




body {
  /* margin: 0;
  padding: 0; */
  font-family: sans-serif;
  /* background-color: #141022; */
  background-color: #282045;
}

h1{
	font-size: 64px;
	font-family: 'Indie Flower', cursive;
}
h2{
	font-family: 'Gaegu', cursive;
}
input[type=button]{
	font-size: 40px;
	border: 1px solid black;
	border-radius: 5px;
	background-color: #373345;
}
input[type=button]:focus{
	outline-width: 0px;
}
input[type=button]:active{
	background-color: #2A2734;
}
input[type=text]{
	text-align: center;
}
a p{
	margin: 0;
	font-family: 'Poor Story', cursive;
}
b{
	color: #262626;
}
p{
	font-family: 'Poor Story', cursive;
	color: #666666;
	font-size: 20px;
}
a{
	text-decoration: none;
}

@media only screen and (max-width: 1100px){
	.g1{
		grid-template-columns: 1fr 1fr;
		grid-template-rows: repeat(3, 120px);
	}
	#searchDiv:nth-child(2){
		grid-column: 1 / span 1;
		grid-row: 2 / span 1;
	}
	#searchDiv:nth-child(3){
		grid-column: 2 / span 1;
		grid-row: 1 / span 1;
	}
}

@media only screen and (max-width: 800px){
	.g1{
		grid-template-rows: repeat(6, 120px);
		grid-template-columns: 1fr;
	}
	#searchDiv:nth-child(2){
		grid-column: 1 / span 1;
		grid-row: 4 / span 1;
	}
	#searchDiv:nth-child(3){
		grid-column: 1 / span 1;
		grid-row: 3 / span 1;
	}
	#searchDiv:nth-child(4){
		grid-column: 1 / span 1;
		grid-row: 2 / span 1;
	}
	#joke{
		font-size: 45px;
	}
}

@media only screen and (max-width: 420px){
	.form-div input{
		max-width: 250px;
	}
	p{
		font-size: 12px;
	}
}
@media only screen and (max-width: 550px){
	p{
		font-size: 16px;
	}
}

@-webkit-keyframes fade-in{
	0%{
		opacity: 0;	
	}
	100%{
		opacity: 1;
	}
}

@keyframes fade-in{
	0%{
		opacity: 0;	
	}
	100%{
		opacity: 1;
	}
}


